.movie {
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
  color: #fff;
  width: 100%;
  height: 100%;
  overflow: hidden;
  /* overflow-y: auto; */
}
.movie:hover {
  background-color: rgba(83, 83, 83, 0.589);
}
.movie-center {
  position: relative;
  flex: 0 0 50%;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  cursor: pointer;
  /* border: 1px solid yellow; */
}
.movie-img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: fill;
}
.movie-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  /* border: 1px solid green; */
}
.movie-info h3 {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1rem;
  padding: 0.25rem;
  overflow-y: auto;
  /* border: 1px solid red; */
}
.footer {
  flex: 0 0 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  text-transform: capitalize;
  width: 100%;
  /* border: 1px solid blue; */
}

.btn-details {
  flex: 0 0 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: #f4f4f4;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
  font-size: 1rem;
  cursor: pointer;
  /* border: 1px solid green; */
}

@media screen and (max-width: 479px) {
  .movie-info h3 {
    /* font-size: 1rem; */
    font-size: 1.1rem;
  }
  .footer {
    flex: 0 0 40px;
    font-size: 1rem;
    /* border: 1px solid green; */
  }

  .btn-details {
    flex: 0 0 40px;
    font-size: 1rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .movie-info h3 {
    font-size: 1.15rem;
  }
  .footer {
    flex: 0 0 42px;
    font-size: 1.05rem;
  }
  .btn-details {
    flex: 0 0 42px;
    font-size: 1.05rem;
  }
}

@media screen and (min-width: 768px) {
  .movie-info h3 {
    font-size: 1.2rem;
  }
  .footer {
    flex: 0 0 44px;
    font-size: 1.1rem;
  }
  .btn-details {
    flex: 0 0 44px;
    font-size: 1.1rem;
  }
}
@media screen and (min-width: 1024px) {
  .movie-info h3 {
    font-size: 1.3rem;
  }
  .footer {
    flex: 0 0 46px;
    font-size: 1.15rem;
  }
  .btn-details {
    flex: 0 0 46px;
    font-size: 1.15rem;
  }
}
@media screen and (min-width: 1280px) {
  .movie-info h3 {
    font-size: 1.4rem;
  }
  .footer {
    flex: 0 0 48px;
    font-size: 1.2rem;
  }
  .btn-details {
    flex: 0 0 48px;
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 1440px) {
  .movie-info h3 {
    font-size: 1.5rem;
  }
  .footer {
    flex: 0 0 50px;
    font-size: 1.25rem;
  }
  .btn-details {
    flex: 0 0 50px;
    font-size: 1.25rem;
  }
}
